body {
  margin: 0;
  padding: 0;
  font: normal 300 100%/1.25 'Source Sans Pro', sans-serif;
  color: #3A405A; }

input, textarea, button {
  font: inherit; }

h1, h2, h3, h4 {
  font: normal 400 100%/1 'Chewy', sans-serif;
  margin: 0; }

h1 {
  font-size: 4em; }
  @media (min-width: 38em) {
    h1 {
      font-size: 9em; } }

h2 {
  font-size: 2em;
  text-align: center; }
  @media (min-width: 38em) {
    h2 {
      font-size: 3em;
      text-align: left; } }

.leading, header p {
  font-size: 1.25em; }

header {
  background-position: right bottom;
  background-size: cover;
  background-image: url("https://github.com/dogtivity/dogtivity/blob/gh-pages/img/hero.jpg?raw=true");
  background-repeat: no-repeat;
  background-color: #4ECDC4;
  color: white;
  height: 60vh;
  text-align: center;
  text-shadow: 0 0 2em rgba(0, 0, 0, 0.5); }
  @media (min-width: 38em) {
    header p {
      font-size: 2em; } }

.start {
  text-align: center;
  padding: 4em 0; }

.container-wrapper {
  width: 100%;
  max-width: 50em;
  margin: auto;
  height: 100%;
  padding: 4em 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box; }

.container-inner {
  padding: 0 2em;
  position: relative; }

@media (min-width: 38em) {
  .container {
    display: table-row; }
    .container-wrapper {
      display: table;
      padding: 6em 0; }
    .container-inner {
      padding: 2em;
      vertical-align: middle;
      display: table-cell; } }

.play {
  position: absolute;
  display: block;
  width: 6em;
  height: 6em;
  bottom: -1em;
  border: 0.25em solid #4ECDC4;
  background: rgba(255, 255, 255, 0.75);
  border-radius: 50%;
  color: #4ECDC4;
  -webkit-transition: all 0.5s ease-in-out;
  -o-transition: all 0.5s ease-in-out;
  transition: all 0.5s ease-in-out; }
  .play:hover {
    -webkit-transform: scale(1.1);
        -ms-transform: scale(1.1);
            transform: scale(1.1);
    background: rgba(255, 255, 255, 0.9); }
  section:nth-child(odd) .play {
    background: rgba(78, 205, 196, 0.75);
    color: #FFFFFF;
    border: 0.25em solid #FFFFFF; }
    section:nth-child(odd) .play:hover {
      background: rgba(78, 205, 196, 0.9); }
  @media (min-width: 38em) {
    .play {
      left: -1em; }
      section:nth-child(odd) .play {
        left: auto;
        right: -1em; } }

section:nth-child(odd) {
  background: #4ECDC4; }

.device {
  max-width: 100%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: 0 2.5em; }
  @media (min-width: 38em) {
    .device {
      max-width: none;
      padding: 0;
      height: 50vh;
      float: left;
      -webkit-transform: rotate(2deg) scale(1.2);
          -ms-transform: rotate(2deg) scale(1.2);
              transform: rotate(2deg) scale(1.2); }
      section:nth-child(odd) .device {
        -webkit-transform: rotate(-2deg) scale(1.2);
            -ms-transform: rotate(-2deg) scale(1.2);
                transform: rotate(-2deg) scale(1.2); } }

section.form-section {
  background: #3A405A;
  color: #FFFFFF; }
  section.form-section .container-inner {
    vertical-align: top; }

form ul {
  list-style: none;
  padding: 0; }
  form ul span {
    display: inline-block;
    padding-left: 0.5em; }
  form ul label + input {
    margin-top: 0.5em; }

@media (min-width: 38em) {
  form .container-inner {
    width: 50%; } }

form .container-inner > label {
  font-size: 1.25em;
  margin: 2em 0 0;
  display: block; }

form .leading, form header p, header form p {
  text-align: center;
  font-weight: 1.5; }

textarea, input[type='text'] {
  width: 100%;
  display: block; }

textarea, input {
  background: #FFFFFF;
  border-radius: 0.25em;
  border: none;
  padding: 0.5em 1em;
  -webkit-box-sizing: border-box;
          box-sizing: border-box; }

input[type='submit'] {
  background: #4ECDC4;
  border: none;
  padding: 0.5em 2em;
  border-radius: 3em;
  margin: 1em 0 0 0;
  display: inline-block; }
  @media (min-width: 38em) {
    input[type='submit'] {
      position: absolute;
      bottom: 2.5em;
      right: 1.5em; } }
  input[type='submit']:hover {
    background: rgba(78, 205, 196, 0.75); }

.footer {
  color: #4ECDC4;
  text-align: center;
  margin-top: 4em; }



